@charset "UTF-8";

@media (min-width:320px){
  .sc_item {
    width: -webkit-calc(50% - 8px);
  }
}
@media (min-width:700px){
  .sc_item {
    width: -webkit-calc(33.3% - 8px);
  }
}

@media (min-width:900px){
  .sc_item {
    width: -webkit-calc(25% - 8px);
  }
}
@media (min-width:1024px){
  .sc_item {
    width: -webkit-calc(20% - 8px);
  }
}

.scroll {
  opacity: 0.3;
  width: 60px;
  height: 60px;
  position: fixed;
  bottom: 10px;
  right: 10px;
  outline: none;
  border: none;
}

.scroll:active {
  opacity: 1;
}

body {
  min-width: 320px;
  font-weight:400;
  font-size:87.5%;
  font-family:'Malgun Gothic','맑은 고딕',Helvetica,'Apple SD Gothic Neo',Dotum,'돋움',sans-serif;
  color:#ececec;
  width: 100%;
  margin:0;
}

address,em,i {
  font-style:normal
}

a {
  color: #000;
  text-decoration:none
}

/**
 * Header
 */

.sc_header {
  align-content: center;
  align-items: stretch;
  background: #3f51b5;
  box-shadow:
          0 4px 5px 0 rgba(0, 0, 0, 0.14),
          0 2px 9px 1px rgba(0, 0, 0, 0.12),
          0 4px 2px -2px rgba(0, 0, 0, 0.2);
  color: #fff;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  font-size: 20px;
  height: 56px;
  justify-content: flex-start;
  position: fixed;
  transition: transform 0.233s cubic-bezier(0, 0, 0.21, 1) 0.1s;
  width: 100%;
  will-change: transform;
  z-index: 1000;
}

.sc_header .sc_logo {
  flex: 1;
  height: 56px;
  width: auto;
  margin: 0;
}

.sc_header .sc_logo img {
  margin-top: 3px;
  margin-left: 16px;
  height: 50px;
  width: auto;
}

.sc_header button {
  border: none;
  cursor: pointer;
  height: 24px;
  margin-right: 16px;
  opacity: 0.54;
  outline: none;
  overflow: hidden;
  text-indent: -30000px;
  transition: opacity 0.333s cubic-bezier(0, 0, 0.21, 1);
  width: 24px;
}

.sc_header .headerButton {
  margin-top: 16px;
}

div {
  display: block;
}

.home {
  background-color: #fff;
  flex: 1;
  overflow-x: hidden;
  overflow-y: auto;
  padding-top: 56px;
}

.list_area ul {
  padding-inline-start: 0px;
  list-style: none;
}

.deals {
  background-color: #fff;

}

.sc_item {
  margin-top: 4px;
  margin-left: 4px;
  margin-right: 4px;
  margin-bottom: 4px;
  padding: 2px 2px;
  float: left;
  position: relative;
  box-sizing: border-box;
  overflow: hidden;
  height: 340px;
}

.sc_item li {
  display: list-item;
  text-align: -webkit-match-parent;
  list-style: none;
}

.sc_item>a:after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 36;
  border: 1px solid rgba(22,24,26,.2);
  pointer-events: none;
  content: '';
}

.sc_item .sc_anchor:hover {
  text-decoration:underline
}

.sc_item .sc_image {
  position: relative;
}

.sc_item .sc_image .sc_thumbnail {
  text-align: center;
}

.sc_item .sc_image .sc_thumbnail img{
  width: 100%;
  max-width: 200px;
  height: auto;
}

.sc_item .sc_desc {
  height: 131px;
}

.sc_item .sc_desc .sc_title {
  display:-webkit-box;
  overflow:hidden;
  height:40px;
  max-height:32px;
  padding:17px 14px 0;
  line-height:16px;
  -webkit-line-clamp:2;
  -webkit-box-orient:vertical
}

.sc_item .sc_desc .sc_price_area {
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  box-sizing:border-box;
  width:100%;
  padding:10px 14px;
  position: absolute;
  bottom: 23px;
  padding-top: 1px;
}

.sc_item .sc_desc .sc_price_area .sc_price_desc {
  float:left;
  font-size:14px;
  color:#f27935;
  letter-spacing:0;
  vertical-align:top
}

.sc_item .sc_desc .sc_price_area .sc_price_desc i {
  float:left;
  font-weight: 700;
}

.sc_item .sc_desc .sc_price_area .sc_original_price {
  margin-top:1px;
  margin-right:4px;
  margin-left:5px;
  font-size:12px;
  color:#c2c7cc;
  text-decoration:line-through
}

.sc_item .sc_desc .sc_price_area .sc_original_price i {
  font-style: normal;
}

.sc_item .sc_desc .sc_price_area .sc_price {
  clear: both;
  font-weight: 700;
  font-size: 15px;
  display: block;
  margin-top: 2px;
  line-height: 1;
  letter-spacing: -.06em;
  float:left;
}

.sc_item .sc_desc .sc_price_area .sc_price i {
  font-style: normal;
}

.sc_item .btn {
  height: 24px;
  width: 24px;
  right: 20px;
  position: absolute;
  border: none;
  outline: none;
}

.sc_sticker_evt{
  position: absolute;
  top:1%;
  left:1%;
  width:68px;
}

.sc_sticker{
  position: absolute;
  bottom:-30px;
  left:1%;
  width:68px;
}

.sc_mall{
  width: 60px;
  height: 30px;
  -webkit-border-radius: 15px;
}

.sc_mall[mall*="티몬"]{
  background: url(https://img.linkprice.com/files/glink/tmon/20170627/5951a3c576be9_120_60.jpg) no-repeat;
  background-size: cover;
}

.sc_mall[mall*="위메프"]{
  background: url('https://image.wemakeprice.com/dealimg2/today_120x60(1).jpg') no-repeat;
  background-size: cover;
}

.sc_mall[mall*="G마켓"]{
  background: url('https://img.linkprice.com/files/stlink/gmarket/0364') no-repeat;
  background-size: cover;
  -webkit-border-radius: 5px;
}

.sc_mall[mall*="옥션"]{
  background: url('https://img.linkprice.com/files/glink/auction/20150901/55e4faabc9f4e_120_60.jpg') no-repeat;
  background-size: cover;
}

.sc_mall[mall*="11번가"]{
  background: url('https://img.linkprice.com/files/glink/11st/20151209/56677fb2594fc_120_60.jpg') no-repeat;
  background-size: cover;
}

.sc_mall[mall*="쿠팡"]{
  background: url('https://img.linkprice.com/files/glink/coupang/20180115/5a5c6d9d5b3f8_120_60.jpg') no-repeat;
  background-size: cover;
  -webkit-border-radius: 10px;
}

.slick-slider * { outline: none; }

.icon_list_popup-content {
  line-height: 45px;
  border-radius: 15px;
  padding: 10px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

.icon_list_popup-content .icon_item {
  border-bottom: 1px solid rgb(187, 187, 187);
}

.icon_list_popup-content .icon {
  height: 32px;
  vertical-align: middle;
  margin-left: 5px;
}

.icon_list_popup-content .icon_only {
  height: 40px;
  vertical-align: middle;
  margin-left: 5px;
}


.icon_list_popup-content .desc {
  margin-left: 5px;
  font-size: 14px;
}

.icon_list_popup-content .sc_anchor:hover {
  text-decoration:underline
}

.wrapped {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.sc_menu_button {
  box-shadow: 0 0 0 2px #ff8edf inset;
  color: #ff8edf;
  background: transparent none;
  text-shadow: none;
  font-size: 1rem;
  font-family: sans-serif;
  cursor: pointer;
  display: inline-block;
  min-height: 1em;
  outline: 0;
  border: none;
  margin: 1em .25em 0 0;
  padding: .78571429em 1.5em .78571429em;
  font-weight: 700;
  line-height: 1em;
  text-align: center;
  text-decoration: none;
  border-radius: .28571429rem;
  transition: opacity .1s ease,background-color .1s ease,color .1s ease,box-shadow .1s ease,background .1s ease,-webkit-box-shadow .1s ease;
}

.sc_menu_button.selected {
  background-color: #ff74d8;
  box-shadow: none;
  color: #fff;
}

.sc_menu_button:hover,.sc_menu_button:active {
  background-color: #ff74d8;
  box-shadow: none;
  color: #fff;
}
